.container {
  display: flex;
  justify-content: center;
}

h1 {
  letter-spacing: 2px;
  font-size: 30px;
  text-transform: uppercase;
}

h2 {
  font-size: 22px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #003b58

}

.btn {
  display: inline-block;
  cursor: pointer;
  border: 1px solid transparent;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  color: #fff;
  background-color: #0071c2;
  align-self: center;
  padding: 10px;

}

p{
  width: 70%;
  text-align: center;
}

button {
  background: white;
  border: none;
  font-size: 18px;
  color: #0071c2
}

